import React from "react";
import Layout from "../../components/shared/Layout/Layout";
import { useSelector } from "react-redux";

const AdminHome = () => {
  const { user } = useSelector((state) => state.auth);
  return (
    <Layout>
      <div className="container">
        <div className="d-felx flex-column mt-4">
          <h1>
            Welcome Admin <i className="text-success">{user?.name}</i>
          </h1>
          <h3>Manage Blood Bank App </h3>
          <hr />
         <p style={{textAlign:"justify"}}>
            Welcome to Blood Bank App, your trusted online platform dedicated to saving lives through blood donation and connecting donors with patients in need. We understand the critical importance of blood in medical emergencies and the ongoing need for a safe and reliable blood supply. Our mission is to bridge the gap between donors and recipients, making the process of blood donation convenient, efficient, and life-saving.
          </p>
           <p style={{textAlign:"justify"}}>
            At Blood Bank App, we believe that every drop of blood counts. Our website serves as a centralized hub where individuals, hospitals, and healthcare organizations can come together to meet the demand for blood donations. Whether you are seeking blood for a medical procedure or wish to contribute as a donor, we are here to assist you every step of the way.

          </p>
           <p style={{textAlign:"justify"}}>
            Our user-friendly website offers a range of features designed to streamline the blood donation process. Donors can easily register online, create personal profiles, and access information about upcoming blood drives, events, and campaigns. By leveraging our intuitive search function, donors can find local donation centers and mobile drives, ensuring their contribution reaches those in need within their community.
            
          </p>
           <p style={{textAlign:"justify"}}>
            For hospitals and healthcare professionals, our website provides a comprehensive platform to request specific blood types, track inventory, and manage blood supply efficiently. We prioritize safety and adhere to strict protocols in all stages of the blood collection, testing, and distribution processes. By maintaining strong partnerships with accredited laboratories and following the highest standards, we guarantee the quality and safety of the blood products provided to healthcare facilities.
          </p>
           <p style={{textAlign:"justify"}}>
            Thank you for visiting Blood Bank App. Join us in our mission to create a world where blood shortages are a thing of the past, and every person in need receives the lifesaving gift they deserve.
          </p>
        </div>
      </div>
    </Layout>
  );
};

export default AdminHome;